:root{
    --border: 30px;
    --padding-panel: 20px;
}

*{
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    color: #204060;
}

body{
    background: #bdd3ca;
    background: -webkit-linear-gradient(to right, #d8f1e7, #bdd3ca);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #d8f1e7, #bdd3ca); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.container{
    height: 100vh;
    width: 100vw;
    display: flex;
    background: #bdd3ca;
    background: -webkit-linear-gradient(to right, #d8f1e7, #bdd3ca);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #d8f1e7, #bdd3ca); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    overflow: hidden;
}

.nav{
    height: 100vh;
    width: 5vw;
    min-width: 70px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.nav-logo{
    height: 70px;
    width: 100%;
    text-align: center;
    overflow: hidden;
}

.nav-logo img{
    width: 50px;
    margin: 10px 0 10px 0;
}

.nav_links_container{
    height: calc(100vh - 70px);
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    overflow: hidden;
}

.nav_links a, .nav_links button, .nav_links img{
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    margin: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    border-radius: 10px;
}

.nav_links button:hover, 
.nav_links button:active, 
.nav_links button:focus, 
.nav_links button:after{
    width: 50px;
    background: #5F7D95;
    border-radius: 30px;
    margin: 0;
    padding: 0;
}

.main{
    height: 100vh;
    width: 95vw;
    overflow: hidden;
}

.header{
    height: 70px;
    width: 100%;
    overflow: hidden;
    
    /* background: red; */
}

.main_panel{
    height: calc(100vh - 70px);
    width: 100%;
    display: flex;
    overflow: hidden;
}

.main_panel1{
    height: inherit;
    width: 70%;
    /* background-color: blue; */
    overflow: hidden;
}

.main_panel2{
    height: inherit;
    width: 30%;
    /* background-color: white; */
    overflow: hidden;
}

.panel_contect{
    /* background-color: lawngreen;     */
    height: inherit;
    width: 100%;
    overflow-y: scroll;
}

@media only screen and (max-width: 900px) {
    .container{
        height: auto;
        width: 100%;
        overflow: none;
    }
    
    .nav{
        height: 10vh;
        min-height: 30px;
        width: 70%;
        position: fixed;
        bottom: 5px;
        margin-left: 15%;
        border-radius: 30px;
    }

    .nav-logo{
        display: none;
    }
    
    .nav_links_container{
        height: inherit;
        width: 100%;
        flex-direction: row;
    }
    
    .main{
        height: auto;
        width: 100%;
        overflow: none;
    }
    
    .header{
    }
    
    .main_panel{
        height: auto;
        display: block;
        overflow: none;
    }
    
    .main_panel1, .main_panel2{
        height: auto;
        width: 100%;
        overflow: none;
    }
    
    .main_panel2{
    }
    
    .panel_contect{
        height: auto;
        width: 100%;
        overflow: initial;
        padding: 0;
    }
}