.box-element-search{
    width: 100%;
    height: auto;
    display: flex;
}

.box-element-search-img{
    width: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.box-element-search-img img{
    width: 50%;
}

.box-element-search-content{
    width: 70%;
    text-align: left;
}

.box-element-search-button{
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-content_header{
    width: 100%;
    display: flex;
}

.search-content_header span:nth-child(1){
    width: 60%;
    text-align: left;
    display: block;
    font-weight: bold;
    font-size: 20px;
    letter-spacing: 1.5px;
    margin: 0 0 10px 10px;
}

.search-content_header span:nth-child(2){
    width: 40%;
    text-align: right;
    display: block;
    color: gray;
}

.box-element-search-content p span{
    font-weight: bold;
}