.header-container{
    height: inherit;
    width: inherit;
    display: flex;
}

.header-title{
    width: 25%;
    display: flex;
    align-items: center;
    font-weight: bold;
    
}

.header-title span:nth-child(1){
    font-size: 22px;
}

.header-search{
    width: 45%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.header-other{
    width: 30%;
    /* background-color: white; */
}

@media only screen and (max-width: 900px){
    .header-container{
    }
    
    .header-title{
        display: none;
    }
    
    .header-search{
        width: 100%;
    }
    
    .header-other{
        display: none;
    }
}