.responsive-chart{
    width: 100% !important;
}

.responsive-chart svg{
    width: 100% !important;
    color: #3fb86d;
}

.title-box-element{
    font-size: 16px;
    letter-spacing: 0.5px;
    display: block;
    width: 100%;
    height: 20px;
}

.color-white{
    color: rgb(212, 212, 212);;
}

/* Top */
.box-element-top-content{
    width: 100%;
    height: 100px;
    display: flex;
}

.box-element-top-icon{
    width: 40%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.box-element-top-icon img{
    width: 70px;
}

.box-element-top-text{
    width: 60%;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.box-element-top-text span:nth-child(1){
    font-weight: bold;
    color: white;
    font-size: 30px;
}

.box-element-top-text span:nth-child(2){
    font-size: 12px;
    color: rgb(212, 212, 212);
    display: block;
}

