.box-element-container{
    width: 100%;
    height: auto;
    padding: 3px 10px 10px 10px;
    overflow: hidden;
}

.box-element-content{
    width: 100%;
    height: auto;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 5px;
    padding: 10px;
    overflow: hidden;
}

.box-element-content:hover,
.box-element-content:active{
    border: 1px dashed #1675e0;
}

.box-element-content-response{

}

@media only screen and (max-width: 992px){
    .box-element-content-response{
        margin: 3px 0px 10px 0px;
    }
}